import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class TypeService {

  constructor() { }






  /**
   * è·åä¸­è±ææ··åå­ç¬¦ä¸²å®ä¹é¿åº¦
   */
  public getStringLocaleLen(text: any) {
    if (!text) {
      return 0;
    }
    const cnCharLen = 3;
    let textLen: number = 0;
    // è±æ
    const regExpEn = /[a-z|A-Z|0-9]|[`|!|@|#|$|%|^|&|*|(|)|\-|_|+|{|}|<|>|?|,|.|/|[|'|;|:|"|\\]/gi;
    // æ¢è¡
    const regExpSpace: any = /\s/g;
    for (let i = 0; i < text.length; i++) {
      regExpEn.lastIndex = 0;
      if (regExpEn.test(text[i]) || regExpSpace.test(text[i])) {
        textLen += 1;
      } else {
        textLen += cnCharLen;
      }
    }
    return textLen;
  }

  /**
   * æç§ä¸­è±ææ··åé¿åº¦æªåå­ç¬¦ä¸²
   */
  localeSubString(text: any, start: number, maxLen: number) {
    if (!text) {
      return '';
    }
    if (start > text.length) {
      return '';
    }
    let cnCharLen = 3;
    let textLen: number = 0;
    let returnStr: string = '';
    // è±æ
    let regExpEn = /[a-z|A-Z|0-9]|[`|!|@|#|$|%|^|&|*|(|)|\-|_|+|{|}|<|>|?|,|.|/|[|'|;|:|"|\\]/gi;
    // æ¢è¡
    let regExpSpace: any = /\s/g;
    for (let i = start; i < text.length; i++) {
      regExpEn.lastIndex = 0;
      if (regExpEn.test(text[i]) || regExpSpace.test(text[i])) {
        textLen += 1;
      } else {
        textLen += cnCharLen;
      }
      if (textLen > maxLen) {
        break;
      } else {
        returnStr += text[i];
      }
    }
    return returnStr;
  }


}
